<template>
  <div class="home">
    <p></p>
    <h1>Nasze rowery</h1>
     <div >
        Wybierz markę
        <p>{{filterM}}</p>
        <div class="form-group">
                <label for="exampleFormControlSelect1">Marka roweru</label>
                <select v-model="filterM" multiple class="form-control" id="exampleFormControlSelect1">
                    <option value="Cannondale">Cannondale</option>
                    <option value="Trek">Trek</option>
                    <option value="GT">GT</option>
                    <option value="KTM">KTM</option>
                    <option value="Ghost">Ghost</option>
                    <option value="Scott">Scott</option>
                    <option value="Author">Author</option>
                    <option value="Merida">Merida</option>
                    <option value="Felt">Felt</option>
                    <option value="Orbea">Orbea</option>
                </select>
            </div>
        <table class="table" id="app2">
            <thead>
            <tr>
                <th scope="col">ID</th>
                <th scope="col">Marka</th>
                <th scope="col">Typ</th>
                <th scope="col">Kolor</th>
                <th scope="col">Przerzutki</th>
                <th scope="col">Dynamo</th>
            </tr>
            </thead>
            
            <tbody v-for="bike in FilteredBikes" :key="bike.id">
                <tr>
                    <td>{{bike.id}}</td>
                    <td>{{bike.marka}}</td>
                    <td>{{bike.typ}}</td>
                    <td>{{bike.kolor}}</td>
                    <td>{{bike.przerzutki}}</td>
                    <td>{{bike.dynamo}}</td>				
                </tr>
            </tbody>
        </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
 
  name: 'Home',
        data(){
            return{
                bikes: [],
                filterM:""
                
            }
				
          },
     
   mounted() {
    axios
      .get("https://my.api.mockaroo.com/bikes.json?key=11b5dd90")
      .then((response) => {
        this.bikes = response.data;
        // alert(this.bikes);
      });
  },
  computed: {
    FilteredBikes: function(){
     if(this.filterM.length==0){
       return this.bikes;
     }else{
        var tempArray = [];
            var ile = this.bikes.length;
                for (let i=0; i<ile; i++) {
                  if (this.bikes[i].marka === this.filterM[0]){
                        tempArray.push(this.bikes[i])
                  }
                }
              return tempArray;
          }
    } 
  }
}
             

</script>
<style scoped>
  .form-group{
    width:33%;
  }
</style>