<template>
    <div class="container">
        <br/>
        <h1>Sales per Types</h1>
        <canvas id="bar-chart2"></canvas>
    </div>
  
</template>

<script>
import Chart from 'chart.js'
import axios from "axios";

export default {
    name: 'Top Types',
    data(){
        return {
            barChart: null,
            barChartData : {type: 'doughnut', 
                            data: {
                labels: [],
                datasets: [
                {
                    label: "Top types",
                    data: [],
                     backgroundColor: [
                    'rgb(255, 99, 132)',
                    'rgb(54, 162, 235)',
                    'rgb(255, 205, 86)',
                    'rgb(94, 168, 50)',
                    'rgb(237, 114, 14)'
                    ],
                    hoverOffset: 4
                }
                ]
            },
            options: {
                responsive: true,
                // lineTension: 1,
                // scales: {
                // yAxes: [
                //     {
                //     ticks: {
                //         beginAtZero: true,
                //         padding: 25
                //     }
                //     }
                // ]
                // }
            }},
        }
    },
     async mounted() {
        const bar = document.getElementById('bar-chart2')
    axios
      .get("https://my.api.mockaroo.com/sales_per_type.json?key=23c43c90")
      .then((response) => {
        var tempArray =[];
        tempArray=response.data;
            var i=0;
                while(i<tempArray.length) {
                     this.barChartData.data.labels.push(tempArray[i].type);
                     this.barChartData.data.datasets[0].data.push(tempArray[i].quantity);  
                     i++;
                }; 
        this.barChart = new Chart(bar, this.barChartData);
        // this.barChart.defaults.global.
        
      });
  }
}
</script>

