import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import TopTypes from '../views/TopTypes.vue'
import BuyerForm from '../views/BuyerForm.vue'
import TopBrands from '../views/TopBrands.vue'
import Marks from '../views/Marks.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/topTypes',
    name: 'TopTypes',
    component: TopTypes
  },
  {
    path:'/buyerForm',
    name:'BuyerForm',
    component: BuyerForm
  },
  {
    path:'/topBrands',
    name:'TopBrands',
    component: TopBrands
  },
  {
    path:'/marks',
    name:'Marks',
    component:Marks
  }
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
