<template>
  <div class="container">
      <p></p>
    <form>
        <h1>Zamów swój rower!</h1>
            <div class="form-group">
                <label for="exampleFormControlSelect1">Marka roweru</label>
                <select v-model="marka" multiple class="form-control" id="exampleFormControlSelect1">
                    <option value="Cannondale">Cannondale</option>
                    <option value="Trek">Trek</option>
                    <option value="GT">GT</option>
                    <option value="KTM">KTM</option>
                    <option value="Ghost">Ghost</option>
                    <option value="Scott">Scott</option>
                    <option value="Author">Author</option>
                    <option value="Merida">Merida</option>
                    <option value="Felt">Felt</option>
                    <option value="Orbea">Orbea</option>
                </select>
            </div>
            <div class="form-group">
                <label for="exampleFormControlSelect2">Typ roweru</label>
                <select v-model="typ" multiple class="form-control" id="exampleFormControlSelect2">
                    <option value="szosowy">szosowy</option>
                    <option value="trekkingowy">trekkingowy</option>
                    <option value="crossowy">crossowy</option>
                    <option value="elektryczny">elektryczny</option>
                    <option value="składany">składany</option>
                </select>
            </div>
            <div class="form-group">
                <label for="exampleFormControlSelect3">Kolor </label>
                <select v-model="kolor" multiple class="form-control" id="exampleFormControlSelect3">
                    <option value="czarny">czarny</option> 
                    <option value="czerwony">czerwony</option>
                    <option value="niebieski">niebieski</option>
                    <option value="szary">szary</option>
                    <option value="zielony">zielony</option>
                </select>
            </div>
            
            <div class="form-check">
                <input v-model="przerzutki" class="form-check-input" type="checkbox" value="" id="defaultCheck1">
                <label class="form-check-label" for="defaultCheck1">
                    Przerzutki
                </label>
            </div>
            <div class="form-check">
                <input v-model="dynamo" class="form-check-input" type="checkbox" value="" id="defaultCheck2">
                <label class="form-check-label" for="defaultCheck2">
                    Dynamo
                </label>
            </div>
            <div class="submit">
                <button @click="checkOrder">Zamów swój rower!</button> 
            </div>
        </form>
  
        <br/>
        <div v-if="marka!=''">
            <p><b>Marka:</b> {{marka}}</p>
        </div>
        <div v-if="typ!=''">
            <p><b>Typ:</b> {{typ}}</p>
        </div>
        <div v-if="kolor!=''">
            <p><b>Kolor:</b> {{kolor}}</p>
        </div>
        <div v-if="przerzutki">
            <p><b>Przerzutki:</b> {{przerzutki}}</p>
        </div>
        <div v-if="dynamo">
            <p><b>Dynamo:</b> {{dynamo}}</p>
        </div>
            
        </div>
</template>
    
  

<script>



export default {
  name: 'BuyerForm',
   data(){
        return{
            marka:'',
            typ:'',
            kolor:'',
            przerzutki:false,
            dynamo:false           
        }
    },
    methods:{
        checkOrder(){
         if(this.marka==''||this.typ==''||this.kolor=='')
         alert("Musisz wybrać markę, typ, i kolor roweru!")
         }
    }
  
 
}
</script>

<style scoped>

form{
    text-align: left;
    font-weight:bold;
    width:500px;
    display: inline-block;
}
.container{
    background-color: white;
    width: 520px;
    border-radius: 5px;
}
button{
    padding: 5px 20px;
    margin-bottom: 10px;
    background-color: steelblue;
    border-radius: 20px;
    font-weight: bold;
    color:white;
    display:block;
    margin:0 auto;   
}
h1{
    font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50
    }
submit{
    text-align: center;
}
</style>
