<script>
	import axios from "axios";
	export default {
	  name: "app",
	  data() {
		return {
		  klienci: [],
			picked: "",
			showRates: false,
		};
	  },
		methods: {
			ToggleShowRates() {
				this.showRates = !this.showRates
			}
		},
	  mounted() {
		axios 
		  .get("https://my.api.mockaroo.com/klienci.json?key=11b5dd90")
		  .then((response) => {
			this.klienci = response.data;
		  });
	  },

	  computed: { 
	  OcenaSklepu: function(){
		//this.klienci.push(this.picked.parseInt());
        var ile = this.klienci.length;
		var suma = 0;
		for (let i=0; i<ile; i++) {
			 suma += this.klienci[i].ocena; 
		}
		var średnia =  suma/ile
		return średnia.toFixed(4);}
	  },
	};
</script>

