<template>
    <div class="container">
        <br/>
        <canvas id="bar-chart"></canvas>
    </div>
  
</template>

<script>
import Chart from 'chart.js'
import axios from "axios";

export default {
    name: 'TopBrands',
    data(){
        return {
            barChart: null,
            barChartData : {type: 'bar', 
                            data: {
                labels: [],
                datasets: [
                {
                    label: "Top brands",
                    data: [],
                    backgroundColor: "rgb(205,92,92)",
                    borderColor: "#36495d",
                    borderWidth: 3
                }
                ]
            },
            options: {
                responsive: true,
                lineTension: 1,
                scales: {
                yAxes: [
                    {
                    ticks: {
                        beginAtZero: true,
                        padding: 25
                    }
                    }
                ]
                }
            }},
        }
    },
     async mounted() {
        const bar = document.getElementById('bar-chart')
    axios
      .get("https://my.api.mockaroo.com/sales_per_bikes.json?key=23c43c90")
      .then((response) => {
        var tempArray =[];
        tempArray=response.data;
            var i=0;
                while(i<tempArray.length) {
                     this.barChartData.data.labels.push(tempArray[i].brand);
                     this.barChartData.data.datasets[0].data.push(tempArray[i].quantity);  
                     i++;
                }; 
        this.barChart = new Chart(bar, this.barChartData);
        
      });
  }
}
</script>

